import React from 'react'
import contact from './imgs/contact.svg'

function FourSection() {
  return (
    <div className="contact-section container flex full-height align-center space-between">
      <img  src={contact}/>
      <span className="main-title bold">Ready to get started?</span>
      <button className="contact-btn">Contact us</button>
    </div>
  );
}

export default FourSection;

