import React from 'react'
import logo from './imgs/logo.png'
import facebook from './imgs/facebook.svg'
import linkedin from './imgs/linkedin.svg'
import pinterest from './imgs/pinterest.svg'
import twitter from './imgs/twitter.svg'


function Footer() {
  return (
    <div className="footer-section container flex align-center ">
<div className="main ">
      <div className="links flex flex-col align-center flex-start">
        <img src={logo} />
        <ul>
          <li>Home</li>
          <li>About</li>
        </ul>
      </div>

      <div className="address">
        <div>987  Hillcrest Lane </div>
        <div>Irvine, CA</div>
        <div>California 92714</div>
        <div>Call Us : 949-833-7432</div>
      </div>

      <div className="flex flex-col space-between" >
        <div className="social-icons">
        <img src={facebook}/>
        <img  src={pinterest}/>
        <img src={linkedin}/>
        <img src={twitter}/>
        </div>
       

        <div className="copy-rights">
        <span>Copyright 2020. All Rights Reserved</span>
      </div>
      </div>
     
      </div>
    </div>
  );
}

export default Footer;

