import React from 'react'
import bgPattern1 from './imgs/bg-pattern-home-1.svg'
import bgPattern2 from './imgs/bg-pattern-home-2.svg'
// main flex flex-row align-center
function FirstSection() {
  return (
   
      <div className="talent-section flex container">
        <img className="talent-img1" src={bgPattern1} />
      <div className="main flex">
        <div className="right align-center flex ">
          <span className="main-title bold">Find the
      <br />
          best<span className="orange-text"> talent</span> 
            </span> 
        </div>
        <div className="left flex">
          <div className="flex align-center">
          <div className='rec'></div>
          <p>Finding the right people and building high performing teams can be hard. Most companies aren’t tapping into the abundance of global talent. We’re about to change that.</p>
          </div>
         
        </div>
        </div>
        <img className="talent-img2" src={bgPattern2} />
      </div>
     
    
  );
}

export default FirstSection;

